let validateForm = {
    /**
     * 
     * @parmas1 
     * return msg ()
     */
    checkEmail(textValue = " ") {
        let result = false;
        let text = textValue.replace(/(^\s*)|(\s*$)/g, "");  // 
        let reg = /^([a-zA-Z]|[0-9]|_)+@([0-9]|[a-zA-Z])+.com$/; // 
        result = reg.test(text);
        return result === true ? "" : "";
    },
    /**
     * length
     * @parmas1  @parmas2 
     * return bool
     */
    checkPassword(textValue = " ", length = 0) {
        let text = textValue.replace(/(^\s*)|(\s*$)/g, "");  //  

        // 
        if (text.length < length) return `${length}` ;

        // 
        let reg = /\d{1,}/g;
        if (reg.test(text) == false) return ``;

        // 
        reg = /[a-zA-Z]{1,}/g;
        if (reg.test(text) == false) return ``;

        // 
        reg = /\s/g;
        if (reg.test(text)) return ``;

        // 
        reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/g;
        if (reg.test(text)) return ``;

        // 
        reg = /[~!@#$%^&*()_+/-{}'"]{1,}/g;
        if (reg.test(text) == false) return ``;

        return '';
    },
    /**
     * length
     * @parmas1  @parmas2 
     * return bool
     */
    checkUserName(textValue = " ", length = 0) {
        let text = textValue.replace(/(^\s*)|(\s*$)/g, "");  //  

        // 
        if (text.length < length) return `${length}`;

        // 
        reg = /\s/g;
        if (reg.test(text)) return  ``;

        // 
        reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/g;
        if (reg.test(text)) return ``;

        return '';
    }
};
module.exports = validateForm;
